"use client";

import Image from "next/image";

export default function ContactPage() {
  return (
    <main className="min-h-screen bg-gradient-to-b from-white via-sky-50/40 to-white py-24 px-6">
      {/* Hero Section */}
      <section className="max-w-7xl mx-auto text-center mb-24">
        <h1 className="text-5xl md:text-6xl font-extrabold text-gray-900 tracking-tight mb-4 bg-gradient-to-r from-sky-600 to-blue-800 bg-clip-text text-transparent">
          Get in Touch
        </h1>
        <p className="text-lg text-gray-700 max-w-2xl mx-auto">
          Have questions or want to collaborate? Reach out to us and we'll get back to you promptly.
        </p>
      </section>

      {/* Contact Card */}
      <section className="max-w-4xl mx-auto bg-white rounded-3xl shadow-2xl overflow-hidden grid md:grid-cols-2">
        {/* Left: Image / Illustration */}
        <div className="relative hidden md:block">
          <Image
            src="/contact-illustration.jpg"
            alt="Contact Illustration"
            fill
            className="object-cover"
          />
          <div className="absolute inset-0 bg-gradient-to-t from-white/80 via-white/60 to-transparent"></div>
        </div>

        {/* Right: Form */}
        <div className="p-10 md:p-16 flex flex-col justify-center">
          <h2 className="text-3xl font-bold text-gray-900 mb-6">Send us a message</h2>
          <form className="space-y-6">
            <div>
              <label className="block text-gray-700 font-medium mb-2">Name</label>
              <input
                type="text"
                placeholder="Your Name"
                className="w-full px-5 py-3 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-sky-400 focus:border-transparent transition"
              />
            </div>
            <div>
              <label className="block text-gray-700 font-medium mb-2">Email</label>
              <input
                type="email"
                placeholder="you@example.com"
                className="w-full px-5 py-3 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-sky-400 focus:border-transparent transition"
              />
            </div>
            <div>
              <label className="block text-gray-700 font-medium mb-2">Message</label>
              <textarea
                placeholder="Your message..."
                className="w-full px-5 py-3 border border-gray-200 rounded-xl focus:outline-none focus:ring-2 focus:ring-sky-400 focus:border-transparent transition h-32 resize-none"
              />
            </div>
            <button
              type="submit"
              className="w-full py-3 bg-gradient-to-r from-sky-600 to-blue-800 text-white font-bold rounded-xl shadow-lg hover:shadow-2xl transition-transform transform hover:-translate-y-1"
            >
              Send Message
            </button>
          </form>
        </div>
      </section>

      {/* Contact Info Section */}
      <section className="max-w-7xl mx-auto mt-24 grid md:grid-cols-3 gap-12 text-center">
        <div className="bg-white rounded-2xl shadow-lg p-8">
          <h3 className="text-xl font-semibold text-gray-900 mb-2">Email</h3>
          <p className="text-gray-600">contact@travelagency.com</p>
        </div>
        <div className="bg-white rounded-2xl shadow-lg p-8">
          <h3 className="text-xl font-semibold text-gray-900 mb-2">Phone</h3>
          <p className="text-gray-600">+94 77 123 4567</p>
        </div>
        <div className="bg-white rounded-2xl shadow-lg p-8">
          <h3 className="text-xl font-semibold text-gray-900 mb-2">Location</h3>
          <p className="text-gray-600">Colombo, Sri Lanka</p>
        </div>
      </section>
    </main>
  );
}
